.contact{
    padding:1.2rem;
}
.contact .row{
    display:flex;
    text-align:center;
    gap:1.5rem;
    flex-wrap: wrap;
}
.contact .row .image{
    flex: 1 1 50rem;
}
.contact .row .image img{
    height:40rem;
    width:100%;
}
.contact .row form{
    flex: 1 1 30rem;
    background-color: var(--white);
    border-radius: .5rem;
    padding:2rem;
    text-align: center;
    margin-right: 1rem;
}
.contact .row form h3{
    font-size:2.5rem;
    margin-bottom: 1rem;
    color:var(--black);
}
.contact .row form .box{
    width:100%;
    margin: 1rem;
    border-radius:.5rem;
    background-color: var(--light-bg);
    padding:1.4rem;
    color:var(--black);
    font-size:1.8rem;
}
.contact .row form textarea{
    height: 20rem;
    resize:none;
}

.contact .box-container{
    margin-top: 3rem;
    display:flex;
    align-items: flex-start;
    gap:1.5rem;
    flex-wrap: wrap;
    padding: 1rem;
}
.contact .box-container .box{
    flex: 1 1 30rem;
    border-radius:.5rem;
    background-color: var(--white);
    padding:2rem;
    text-align: center;
}
.contact .box-container .box i{
    font-size: 3rem;
    color: var(--main-color);
    margin-bottom: 1rem;
}
.contact .box-container .box h3{
    margin: 1.5rem 0;
    font-size:2rem;
    color: var(--black);
}
.contact .box-container .box a{
    display:block;
    font-size: 1.7rem;
    color: var(--light-color);
    line-height:1.5;
    margin-top:.5rem;
}
.contact .box-container .box a:hover{
    text-decoration: underline;
    color:var(--main-color);
}