.about{
    padding: 2rem;
}
.about .row{
    display:flex;
    align-items: center;
    gap:1.5rem;
    flex-wrap:wrap;
}
.about .row .image{
    flex:1 1 40rem;
}
.about .row .image img{
    width:100%;
    height:40rem;
}
.about .row .content{
    flex:1 1 40rem;
    text-align: center;
}
.about .row .content h3{
    font-size:2.5rem;
    color:var(--black);
}
.about .row .content p{
    line-height: 2;
    font-size:1.7rem;
    color: var(--light-color);
    padding:1rem 3rem;
}

.about .box-container{
    margin-top:3rem;
    display:flex;
    gap:1.5rem;
    flex-wrap: wrap;
}
.about .box-container .box{
    flex: 1 1 25rem;
    display:flex;
    background-color: var(--white);
    border-radius:.5rem;
    padding:2rem;
    align-items: center;
    gap: 2rem;
}
.about .box-container .box i{
    font-size: 3rem;
    color:var(--black);
}
.about .box-container .box h3{
    color:var(--main-color);
    font-size: 2.5rem;
    margin-bottom: .2rem;
}
.about .box-container .box span{
    font-size:1.6rem;
    color:var(--light-color);
}